import React from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import axios from 'axios';
import { store } from 'react-notifications-component';

class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.state = {
      email: '',
      message: '',
      toSend: true
    }
  }

  handleEmailChange = event => {
    this.setState({ email: event.target.value });
  };

  handleMessageChange = event => {
    this.setState({ message: event.target.value });
  };

  

  handleSubmit(event) {
    event.preventDefault();
    this.setState({toSend: false})
    var dataToPost = {
      email: event.target.email.value,
      message: event.target.message.value,
    };

    axios
      .post(process.env.REACT_APP_BACK + '/contact', dataToPost)
      .then(res => {
        })
      .catch(error => {
        store.addNotification({
          title: error.type,
          message: error.message,
          type: 'danger',
          insert: 'top',
          container: 'bottom-right',
          animationIn: ['animated', 'fadeIn'],
          animationOut: ['animated', 'fadeOut'],
        });
      });
  }

  render() {
    return (
      <div>{this.state.toSend ? (
        <div id="ContactForm">
        <h1 id="ContactTitle">Skontaktuj si z nami!</h1>
        <form onSubmit={this.handleSubmit}>
          <Form.Group className="ContactInput" controlId="email">
            <Form.Label>Podaj swj mail, abymy mogli Ci odpowiedzie</Form.Label>
            <Form.Control
              type="email"
              className={'form-control'}
              value={this.state.email}
              onChange={this.handleEmailChange}
              placeholder="uytkownik@domena.com"
            />
          </Form.Group>

          <Form.Group className="ContactInput" controlId="message">
            <Form.Label>Wiadomo</Form.Label>
            <Form.Control
              type="text"
              className={'form-control'}
              value={this.state.message}
              onChange={this.handleMessageChange}
              placeholder="Napisz prosz w czym moemy Ci pomc"
            />
          </Form.Group>

          <Button
            className="RegisterInput"
            variant="primary"
            type="submit"
            disabled={
              this.state.email === '' ||
              this.state.message === ''
            }
          >
            Wylij wiadomo!
          </Button>
        </form>
      </div>
      ): (
          <div>Dzikujemy! Dziki Tobie bdziemy mieli co robi w wolnym czasie!</div>
      )}</div>
      
    );
    }
}

export default Contact;
